import React from 'react';
import './icon.scss';

export type IconProps = {
  title:
    | 'react'
    | 'redux'
    | 'sass'
    | 'styled-components'
    | 'mongo'
    | 'node'
    | 'html'
    | 'css'
    | 'figma'
    | 'typescript';
  className?: string;
  imgClassName?: string;
  size?: number;
};

const icons = require.context(
  'assets/skill-icons',
  false,
  /\.(png|jpe?g|svg)$/
);

const Icon = ({ title, className, imgClassName, size = 50 }: IconProps) => (
  <div
    style={
      !className
        ? {
            width: `${size / 10}rem`,
            height: `${size / 10}rem`,
          }
        : undefined
    }
    className={`Icon ${className}`}
  >
    <img
      alt={`${title} icon`}
      src={icons(`./${title}.svg`).default}
      className={imgClassName}
    />
  </div>
);

export default Icon;
